@media print {
  .app {
    width: 98%;
  }

  .buttons {
    display: none;
  }

  .flex-container > div {
    border: 1px solid gray;
  }

  thead {
    border-bottom: 1px solid black;
  }

  td {
    border: 1px solid black;
  }

  table {
    width: 98%;
  }

  input {
    border-style: none;
  }

}

