.grid {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    /* grid-template-columns: 200px 200px 200px 200px 230px; */
}

.grid label {
    font-size: 14px;
    color: black;
    text-decoration: underline;
}

.grid input {
    color: rgb(65, 63, 63);
    height: 25px;
    font-size: 14px;
    border: 1px solid black;
    text-transform: uppercase;
}